import {Component, ViewChild, ViewContainerRef} from '@angular/core';
import {InfoBoxComponent} from './info-box/info-box.component';
import {MessageService} from './message.service';
import {ModalService} from '../shared/modal/modal.service';

@Component({
  selector: 'app-home',
  styleUrls: ['./home.component.scss'],
  templateUrl: './home.component.html',
})
export class HomeComponent {

  message = 'INIT';
  name = 'START_';
  reply = '';

  @ViewChild('child')
  private child: InfoBoxComponent;

  constructor(private messageService: MessageService,
              private hostElement: ViewContainerRef,
              private modalService: ModalService) {}

  changeChild() {
    this.name = 'Changed by Script';
  }

  proccessReply(message: string) {
    this.message = message;
  }

  proccessReplyFromCode() {
    this.child.reply('Send message from parent via CODE');
  }

  sendMessage() {
    this.messageService.sendMessage('Hallo neue Nachricht!');
  }

  openModal() {
    const modal = this.modalService.open(
        { message: this.name, title: 'Modal Title', type: 'primary' },
        this.hostElement
    );

    modal.close.subscribe(_ => {
      console.log('MODAL closed');
    });

    modal.cancel.subscribe(_ => {
      console.log('MODAL cancelled');
    });
  }

  openModalGlobal() {
    this.modalService.openGlobal({
      title: 'Glboal Error',
      message: 'Please contact the support',
      type: 'warn'
    }).subscribe(modal => {
      modal.close.subscribe(() => console.log('Global MODAL closed'));
      modal.cancel.subscribe(() => console.log('Global MODAL cancelled'));
    });
  }
}
